.home-left {
    display: flex;
    width: 40%;
    flex-direction: column;
    margin-left: 10%;
}

.home {
    padding-top: 100px;
    width: 100%;
    display: flex;
    background-color: #155658ab;
    justify-content: space-evenly;
    align-items: center;
}

.frontend {
    display: flex;
    flex-direction: column;
    color: #f5ce12;
    font-size: 60px;
    padding: 35px 0;
}

.home-title {
    font-size: 35px;
    color: #efefcd3d;
}

.some-text {
    width: 60%;
    font-size: 20px;
    color: #efefcdfd;
    padding-bottom: 100px;
}

.home-right {
    width: 40%;
    justify-content: center;
    align-items: center;
    padding-bottom: 5%;
}

.divforimage {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    justify-content: center;

}

.imageofme {
    width: 50%;
    background-color: #f5cf12bd;
    border-radius: 45%;
    opacity: 0.9;

}


.area {
    background: -webkit-linear-gradient(to left, #8f94fb, #4e54c8);
}

.circles {
    top: 0;
    left: 0;
    width: 100%;
    height: 55%;
    overflow: hidden;
}

.circles li {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(192, 175, 175, 0.5);
    animation: animate 25s linear infinite;
    top: 0;
}

.circles li:nth-child(1) {
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}


.circles li:nth-child(2) {
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.circles li:nth-child(3) {
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
}

.circles li:nth-child(4) {
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.circles li:nth-child(5) {
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.circles li:nth-child(6) {
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.circles li:nth-child(7) {
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.circles li:nth-child(8) {
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.circles li:nth-child(9) {
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.circles li:nth-child(10) {
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}



@keyframes animate {

    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100% {
        transform: translateY(500px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}