.circles {
  top: 0;
  left: 0;
  width: 100%;
  height: 55%;
  overflow: hidden;
}

.circles li {
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(192, 175, 175, 0.5);
  -webkit-animation: animate 25s linear infinite;
          animation: animate 25s linear infinite;
  top: 0;
}

.circles li:nth-child(1) {
  left: 25%;
  width: 80px;
  height: 80px;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.circles li:nth-child(2) {
  left: 10%;
