.header {
    background-color: #056070;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 20px 0;
}

.less {
    color: #8dbc20;
    font-size: 45px;
}

.more {
    color: #8dbc20;
    font-size: 45px;
}

.me {
    color: #f5ce12;
    font-size: 45px;
}

.headerLogo {
    display: flex;
    justify-content: center;
}

.header-ul {
    font-size: 30px;
    display: flex;
    justify-content: space-between;
    color: #efefcd;
}

.HeaderNav {
    width: 45%;
}